<!-- eslint-disable vue/no-multiple-template-root -->
<template>
    <div id="page-wrap">
        <div v-for="product in cartItems" :key="product.id" class="product-container">
            <img :src="product.imageUrl" id="product-image">
            <div class="details-wrap">
                <h3>{{ product.name }}</h3>
                <p>{{ product.price }}</p>
            </div>
            <button class="remove-button">
                Remove from Cart
            </button>
        </div>
        <div id="total-price">
            <h3>Total: ${{ totalPrice }}</h3>
            <button id="checkout-button">Proceed to CheckOut</button>
        </div>
    </div>
</template>

<script>
import { cartItems } from '../fake-data';
export default {
    name: 'CartPage.vue',
    data() {
        return {
            cartItems,
        };
    },
    computed: {
        totalPrice() {
            return this.cartItems.reduce((acc, item) => acc + Number(item.price), 0);
        },
    },
};
</script>

<style scoped>
h1 {
    border-bottom: 1px solid black;
    margin: 0;
    margin-top: 16px;
    padding: 16px;
}

#total {
    text-align: right;
    padding: 16px;
}

#check-out {
    width: 100%;
}
</style>